.header__nav {
  display: flex;
  flex-direction: row;
  align-items: center;
}

.header__nav_isLogin {
  display: flex;
  flex-direction: row;
  align-items: center;
}

@media screen and (max-width: 767px) {
  .header__nav_isLogin {
    display: grid;
    order: -1;
    border-bottom: 1px solid rgba(84, 84, 84, 0.7);
    width: 320px;
    justify-items: center;
  }
}

